.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
}

.card__content {
  margin-top: 40px;
}

.card__name {
  width: 100%;
  font-weight: 500;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews {
  justify-content: space-between;
  align-items: end;
  margin-top: 16px;
  display: flex;
}

.card__reviews-text {
  text-align: right;
  font-size: 10px;
  line-height: 140%;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
}

.card__price-value {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.card__buy-button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 114.29%;
  text-decoration: none;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 150%;
}

body * {
  color: #060b35;
  font-size: 12px;
  font-weight: 400;
}

h1, h2, h3, p, a {
  margin: 0;
}

